<template>
  <el-carousel :interval="5000" arrow="always" height="200px" indicator-position="none">
    <el-carousel-item v-for="(e,i) in imgarr" :key="i">
      <img :src="e">
    </el-carousel-item>
  </el-carousel>
</template>

<script>
export default {
  name: "slidecommon",
  data() {
    return {
      imgarr: [
        require("../assets/common1.jpg"),
        require("../assets/common2.jpg"),
        require("../assets/common3.jpg"),
        require("../assets/common4.jpg"),
        require("../assets/common5.jpg"),
        require("../assets/common6.jpg")
      ]
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* slider */
.el-carousel__item img {
  width: 100%;
  opacity: 0.75;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
</style>



